import * as v from './styles/styleVariables'

const styles = {
  app: {
    width: `${v.ms1 * Math.pow(v.ratio, 15)}rem`, // 74.551rem; 1192.8px
    maxWidth: '92%',
    marginLeft: 'auto',
    marginRight: 'auto'
  }
}

export default styles



